@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-13xl: 32px;
  --font-size-sm: 14px;
  --font-size-5xl: 24px;
  --font-size-xl: 20px;
  --font-size-21xl: 40px;
  --font-size-2xs: 11px;
  --font-size-lg: 18px;

  /* Colors */
  --color-gray-100: #242424;
  --color-lightblue: #bbddf5;
  --color-dimgray: #6b6b6b;
  --color-moccasin: #ffe8b6;
  --color-turquoise-100: rgba(0, 201, 184, 0.54);
  --color-black: #000;
  --color-lightpink: #ffb2c1;
  --color-tomato: rgba(238, 94, 84, 0.82);
  --color-white: #fff;
  --color-lavender: #e7e8f2;
  --color-whitesmoke-100: #f2f2f2;

  /* Gaps */
  --gap-0: 0px;
  --gap-9xs: 4px;

  /* Paddings */
  --padding-10xs: 3px;
  --padding-2xs-8: 10.8px;
  --padding-4xs: 9px;
  --padding-5xs: 8px;
  --padding-178xl-1: 197.1px;
  --padding-2xl-3: 21.3px;
  --padding-7xs: 6px;
  --padding-8xs: 5px;

  /* border radiuses */
  --br-81xl: 100px;
  --br-mini: 15px;
  --br-7xs: 6px;
  --br-smi: 13px;
}
